<template>
  <div class="setting-name-view">
    <header-view :title="title" :fixed="fixed" :rightText="rightText" @rightClick="parentEvent"></header-view>
    <div class="name-input"><input type="text" v-model="param"><i class="clear-input" v-show="isshow" @click="clear"></i></div>
    <div class="input-help">{{help}}</div>
  </div>
</template>

<script>
  import {postMemberUpdate} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        param: '',
        fixed: false,
        title: '修改昵称',
        help: '以英文字母或汉字开头，限6-16个字符',
        rightText: '确定'
      }
    },
    created: function () {
      this.param = this.$route.params.setparams === undefined ? localStorage.userName : this.$route.params.setparams
    },
    computed: {
      isshow: function () {
        if (this.param === '') {
          return false
        } else {
          return true
        }
      }
    },
    methods: {
      parentEvent: function () {
        var _this = this
        postMemberUpdate(this.param).then(function (rep) {
          if(rep.data.code =='1'){
            _this.$toast('修改成功！')
            _this.$router.push({
              name: 'custom'
            })
          }else{
            _this.$toast('操作失败，服务器返回数据异常！')
          }
        }).catch(function () {
            _this.$toast('操作失败，连接服务器异常！')
        })
        
      },
      clear: function () {
        this.param = ''
      }
    }
  }
</script>

<style>
/* 修改昵称页面 */

.setting-name-view {
  height: 100%;
  background-color: #F5F5F5;
}

.name-input {
  position: relative;
  height: .55rem;
  padding-left: .15rem;
  padding-right: .48rem;
  background-color: #FFFFFF;
}

.name-input input {
  height: 100%;
  width: 100%;
}

.clear-input {
  position: absolute;
  right: .18rem;
  height: .3rem;
  width: .3rem;
  top: 50%;
  transform: translateY(-50%);
  background: url(/static/image/selfset/name_set_close@3x.png);
  background-size: 100% 100%;
}

.input-help {
  padding: .05rem .15rem;
  font-size: .12rem;
}
</style>


